import React, { useState } from 'react';
import {
  Box,
  Typography,
  TextField,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid,
} from '@mui/material';
import { useNavigate } from 'react-router-dom';
import ChartDisplay from './ChartDisplay';

function DataConfig() {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [xValue, setXValue] = useState('');
  const [yValue, setYValue] = useState('');
  const [chartType, setChartType] = useState('bar');
  const [chartTitle, setChartTitle] = useState('');
  const [xAxisLabel, setXAxisLabel] = useState('');
  const [yAxisLabel, setYAxisLabel] = useState('');
  const [chartColor, setChartColor] = useState('#1976d2');
  const [showChart, setShowChart] = useState(false);

  const handleAddData = () => {
    if (xValue && yValue) {
      const newData = [...data, { x: xValue, y: parseFloat(yValue) }];
      setData(newData);
      setXValue('');
      setYValue('');
    }
  };

  const handleRemoveData = (index) => {
    const newData = data.filter((_, i) => i !== index);
    setData(newData);
  };

  const handleGenerateChart = () => {
    if (data.length > 0) {
      setShowChart(true);
    }
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" gutterBottom>
         
      </Typography>
      
      <Grid container spacing={3}>
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" gutterBottom>
               
            </Typography>
            
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel> </InputLabel>
              <Select
                value={chartType}
                label=" "
                onChange={(e) => setChartType(e.target.value)}
              >
                <MenuItem value="bar"></MenuItem>
                <MenuItem value="pie"> </MenuItem>
                <MenuItem value="line"> </MenuItem>
                <MenuItem value="scatter"> </MenuItem>
                <MenuItem value="area"> </MenuItem>
                <MenuItem value="bubble"> </MenuItem>
                <MenuItem value="radar"></MenuItem>
                <MenuItem value="doughnut"></MenuItem>
                <MenuItem value="polarArea"> </MenuItem>
                <MenuItem value="funnel"> </MenuItem>
                <MenuItem value="heatmap"> </MenuItem>
                <MenuItem value="treemap"> </MenuItem>
                <MenuItem value="stackedBar"> </MenuItem>
                <MenuItem value="timeline"></MenuItem>
                <MenuItem value="waterfall"></MenuItem>
                <MenuItem value="candlestick"></MenuItem>
              </Select>
            </FormControl>

            <TextField
              fullWidth
              label=" "
              value={chartTitle}
              onChange={(e) => setChartTitle(e.target.value)}
              sx={{ mb: 2 }}
            />

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>  </InputLabel>
              <Select
                value={xAxisLabel}
                label="  "
                onChange={(e) => setXAxisLabel(e.target.value)}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=" (C)"> (C)</MenuItem>
                <MenuItem value=" (F)"> (F)</MenuItem>
                <MenuItem value=" (K)"> (K)</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ($)"> ($)</MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=" (%)"> (%)</MenuItem>
                <MenuItem value=" (m)"> (m)</MenuItem>
                <MenuItem value=" (cm)"> (cm)</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" (kg)"> (kg)</MenuItem>
                <MenuItem value=" (g)"> (g)</MenuItem>
                <MenuItem value=" (L)"> (L)</MenuItem>
                <MenuItem value=" (ml)"> (ml)</MenuItem>
                <MenuItem value=" (km/h)"> (km/h)</MenuItem>
                <MenuItem value=" (m/s)"> (m/s)</MenuItem>
                <MenuItem value=" "> </MenuItem>
              </Select>
            </FormControl>

            {xAxisLabel === ' ' && (
              <TextField
                fullWidth
                label="   "
                value={xAxisLabel}
                onChange={(e) => setXAxisLabel(e.target.value)}
                sx={{ mb: 2 }}
              />
            )}

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>  </InputLabel>
              <Select
                value={yAxisLabel}
                label="  "
                onChange={(e) => setYAxisLabel(e.target.value)}
              >
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=" (C)"> (C)</MenuItem>
                <MenuItem value=" (F)"> (F)</MenuItem>
                <MenuItem value=" (K)"> (K)</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ($)"> ($)</MenuItem>
                <MenuItem value=""></MenuItem>
                <MenuItem value=" (%)"> (%)</MenuItem>
                <MenuItem value=" (m)"> (m)</MenuItem>
                <MenuItem value=" (cm)"> (cm)</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" ()"> ()</MenuItem>
                <MenuItem value=" (kg)"> (kg)</MenuItem>
                <MenuItem value=" (g)"> (g)</MenuItem>
                <MenuItem value=" (L)"> (L)</MenuItem>
                <MenuItem value=" (ml)"> (ml)</MenuItem>
                <MenuItem value=" (km/h)"> (km/h)</MenuItem>
                <MenuItem value=" (m/s)"> (m/s)</MenuItem>
                <MenuItem value=" "> </MenuItem>
              </Select>
            </FormControl>

            {yAxisLabel === ' ' && (
              <TextField
                fullWidth
                label="   "
                value={yAxisLabel}
                onChange={(e) => setYAxisLabel(e.target.value)}
                sx={{ mb: 2 }}
              />
            )}

            <TextField
              fullWidth
              label=" "
              type="color"
              value={chartColor}
              onChange={(e) => setChartColor(e.target.value)}
              sx={{ mb: 2 }}
            />
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, mb: 3 }}>
            <Typography variant="h6" gutterBottom>
               
            </Typography>
            
            <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
              <TextField
                label={`${xAxisLabel || ' X'}`}
                value={xValue}
                onChange={(e) => setXValue(e.target.value)}
                type={xAxisLabel === '' ? 'date' : 'number'}
                inputProps={{ 
                  step: xAxisLabel === '' ? undefined : 1,
                  min: xAxisLabel === '' ? undefined : 0
                }}
                fullWidth
              />
              <TextField
                label={`${yAxisLabel || ' Y'}`}
                value={yValue}
                onChange={(e) => setYValue(e.target.value)}
                type="number"
                inputProps={{ step: 1, min: 0 }}
                fullWidth
              />
              <Button variant="contained" onClick={handleAddData}>
                 
              </Button>
            </Box>

            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>{xAxisLabel || " "}</TableCell>
                    <TableCell>{yAxisLabel || " "}</TableCell>
                    <TableCell></TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {data.map((item, index) => (
                    <TableRow key={index}>
                      <TableCell>{item.x}</TableCell>
                      <TableCell>{item.y}</TableCell>
                      <TableCell>
                        <Button
                          color="error"
                          onClick={() => handleRemoveData(index)}
                        >
                          
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </Grid>

      {showChart && (
        <ChartDisplay
          data={data}
          chartType={chartType}
          chartTitle={chartTitle}
          xAxisLabel={xAxisLabel}
          yAxisLabel={yAxisLabel}
          chartColor={chartColor}
        />
      )}

      <Box sx={{ display: 'flex', justifyContent: 'center', gap: 2, mt: 3 }}>
        <Button variant="outlined" onClick={() => navigate('/')}>
          
        </Button>
        <Button 
          variant="contained" 
          onClick={handleGenerateChart}
          disabled={data.length === 0}
        >
           
        </Button>
      </Box>
    </Box>
  );
}

export default DataConfig; 